<template>
  <!-- <input type="file" @change="onChange" /> -->
  <input ref="imageInput" type="file" accept="image/*" @change="onChange" />
</template>

<script>
/*! Copyright (c) 2016 Naufal Rabbani (http://github.com/BosNaufal)
 * Licensed Under MIT (http://opensource.org/licenses/MIT)
 *
 * Vue Image Compressor @ Version 0.0.1
 *
 * refs:
 * https://developer.mozilla.org/en-US/docs/Web/API/FileReader/readAsDataURL
 * https://davidwalsh.name/convert-canvas-image
 * https://beta.webcomponents.org/element/3mp3ri0r/cpol-image
 *
 */
import base64toblob from 'base64toblob'
export default {
  props: {
    // Image Scale Percentage (1 - 100)
    scale: {
      type: Number,
      default: 100
    },
    // Image Scale Percentage (1 - 100)
    quality: {
      type: Number,
      default: 100
    },
    // Pass the files info when it's done
    done: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      file: {},
      result: {},
      reader: {},
      imgSrc: ''
    }
  },
  watch: {
    // When Scale and Quality properties has changed, do Redraw
    scale() {
      return this.redraw()
    },
    quality() {
      return this.redraw()
    }
  },
  methods: {
    /*
        When Input File has changed
      */
    onChange(e) {
      // If There's no file choosen
      const file = e.target.files[0]
      if (!file) return false
      // get the file
      this.file = e.target.files[0]
      // Validation
      const type = this.file.type
      const valid = type.includes('image')
      // if (!valid) throw 'File Type Is Not Supported. Upload an image instead'
      // Make new FileReader
      this.reader = new FileReader()
      // Convert the file to base64 text
      this.reader.readAsDataURL(this.file)
      // on reader load somthing...
      this.reader.onload = this.fileOnLoad
    },
    /*
        Draw And Compress The Image
        @params {String} imgUrl
      */
    drawImage(imgUrl) {
      // Recreate Canvas Element
      const canvas = document.createElement('canvas')
      this.canvas = canvas
      // Set Canvas Context
      const ctx = this.canvas.getContext('2d')
      // Create New Image
      const img = new Image()
      img.src = imgUrl
      img.onload = () => {
        // Image Size After Scaling
        const scale = this.scale / 100
        const width = img.width * scale
        const height = img.height * scale
        // Set Canvas Height And Width According to Image Size And Scale
        this.canvas.setAttribute('width', width)
        this.canvas.setAttribute('height', height)
        ctx.drawImage(img, 0, 0, width, height)
        // Quality Of Image
        const quality = this.quality ? this.quality / 100 : 1
        // If all files have been proceed
        const base64 = this.canvas.toDataURL('image/jpeg', quality)
        let fileName = this.result.file.name
        const lastDot = fileName.lastIndexOf('.')
        fileName = fileName.substr(0, lastDot) + '.jpeg'
        const objToPass = {
          canvas: this.canvas,
          original: this.result,
          compressed: {
            blob: this.toBlob(base64),
            base64,
            name: fileName,
            file: this.buildFile(base64, fileName)
          }
        }

        const asize = Math.round(objToPass.compressed.file.size / 1000)
        objToPass.compressed.size = asize
        objToPass.compressed.unit = 'kb'
        objToPass.compressed.type = 'image/jpeg'
        this.done(objToPass)
        this.$refs.imageInput.value = null
      }
    },
    /*
        Redraw the canvas
      */
    redraw() {
      if (this.result.base64) {
        this.drawImage(this.result.base64)
      }
    },
    /*
        When The File in loaded
      */
    fileOnLoad() {
      // The File
      const { file } = this
      // Make a fileInfo Object
      const fileInfo = {
        name: file.name,
        type: file.type,
        size: Math.round(file.size / 1000) + ' kB',
        base64: this.reader.result,
        file
      }
      // Push it to the state
      this.result = fileInfo
      // DrawImage
      this.drawImage(this.result.base64)
    },
    // Convert Base64 to Blob
    toBlob(imgUrl) {
      const blob = base64toblob(imgUrl.split(',')[1], 'image/jpeg')
      const url = window.URL.createObjectURL(blob)
      return url
    },
    buildFile(base64, name) {
      const blob = base64toblob(base64.split(',')[1], 'image/jpeg')
      return new File([blob], name, { type: 'image/jpeg' })
    }
    // Convert Blob To File
    // buildFile(blob, name) {
    //   return new File([blob], name)
    // }
  }
}
</script>
