<template>
  <section class="main">
    <div class="container main-container is-white left-main">
      <div class="left-container">
        <div class="widget">
          <div class="widget-header">
            <nav class="breadcrumb">
              <ul>
                <li><a href="/">首页</a></li>
                <li>
                  <a :href="'/user/' + currentUser.id + '?tab=topics'">{{
                    currentUser.nickname
                  }}</a>
                </li>
                <li class="is-active">
                  <a href="#" aria-current="page">主题</a>
                </li>
              </ul>
            </nav>
          </div>
          <div class="widget-content">
            <div class="field is-horizontal">
              <div class="field-body">
                <div class="field" style="width:100%;">
                  <input
                    v-model="postForm.title"
                    class="input is-small"
                    type="text"
                    placeholder="请输入标题"
                  />
                </div>
                <!-- <div class="field">
                  <div class="select">
                    <select v-model="postForm.nodeId">
                      <option value="0">选择节点</option>
                      <option
                        v-for="node in nodes"
                        :key="node.nodeId"
                        :value="node.nodeId"
                        >{{ nodeName }}</option
                      >
                    </select>
                  </div>
                </div> -->
              </div>
            </div>

            <!-- <div class="field">
              <div class="control">
                <markdown-editor
                  v-model="postForm.content"
                  editor-id="topicEditEditor"
                  placeholder="可空，将图片复制或拖入编辑器可上传"
                />
              </div>
            </div> -->
            <div class="field">
              <label class="label">详情:</label>
              <div class="control">
                <textarea
                  v-model="postForm.content"
                  class="textarea is-small"
                  placeholder="详情内容建议包含：个人是否为租客、是否个人房源、房租位置、大小、户型、房客情况、价格、租期可否续租，联系方式等等"
                ></textarea>
              </div>
            </div>
            <div class="field">
              <label class="label">图片:</label>
              <div class="control">
                <post-tweets
                  :imageList="postForm.imageList"
                  :onImageUploadCallBack="onImageUpload"
                ></post-tweets>
              </div>
            </div>
            <div class="field">
              <label class="label">地址标签:</label>
              <div class="control">
                <input
                  v-model="postForm.extraData.address"
                  class="input is-small"
                  type="text"
                  placeholder="小区名称或标志性地点（请写正确，方便距离计算，搜索匹配）"
                />
              </div>
            </div>
            <!-- <div class="field">
              <div class="control">
                <tag-input v-model="postForm.tags" />
              </div>
            </div> -->

            <div class="field is-grouped">
              <div class="control">
                <a
                  :class="{ 'is-loading': publishing }"
                  :disabled="publishing"
                  class="button is-success"
                  @click="submitCreate"
                  >提交更改</a
                >
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="right-container">
