      </div>
    </div>
  </section>
</template>

<script>
import utils from '~/common/utils'
import TagInput from '~/components/TagInput'
import MarkdownHelp from '~/components/MarkdownHelp'
// import MarkdownEditor from '~/components/MarkdownEditor'
import PostTweets from '~/components/PostTweets'
import jsonConventer from '~/common/jsonConventer'
export default {
  middleware: 'authenticated',
  components: {
    // TagInput,
    MarkdownHelp,
    PostTweets
    // MarkdownEditor
  },
  async asyncData({ $axios, params }) {
    const [topic, nodes] = await Promise.all([
      $axios.get('/api/topic/edit/' + params.id),
      $axios.get('/api/topic/nodes')
    ])

    topic.extraData = JSON.parse(topic.extraData)
    let images = []
    if (topic.imageList) {
      images = topic.imageList.split(',')
    }
    console.log('topic:' + JSON.stringify(topic))
    return {
      topic,
      nodes,
      postForm: {
        nodeId: topic.nodeId,
        title: topic.title,
        tags: topic.tags,
        content: topic.content,
        imageList: images,
        extraData: topic.extraData
      }
    }
  },
  data() {
    return {
      publishing: false, // 当前是否正处于发布中...
      imageList: [],
      postForm: {
        nodeId: 0,
        title: '',
        tags: [],
        imageList: [],
        content: ''
      }
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.user.current
    }
  },
  mounted() {},
  methods: {
    onImageUpload(imageList) {
      this.postForm.imageList = imageList
      // console.log('extraData:' + JSON.stringify(this.postForm.extraData))
    },
    async submitCreate() {
      const me = this
      if (me.publishing) {
        return
      }
      me.publishing = true

      try {
        const transferContent = jsonConventer.homeItemToJsonString(
          '北京',
          this.postForm.extraData.address
        )
        const topic = await this.$axios.post(
          '/api/topic/edit/' + this.topic.topicId,
          {
            nodeId: this.postForm.nodeId,
            title: this.postForm.title,
            content: this.postForm.content,
            tags: this.postForm.tags ? this.postForm.tags.join(',') : '',
            imageList: this.postForm.imageList.toString(),
            extraData: JSON.stringify(transferContent)
          }
        )
        this.$toast.success('提交成功', {
          duration: 1000,
          onComplete() {
            utils.linkTo('/rent/' + topic.topicId)
          }
        })
      } catch (e) {
        console.error(e)
        me.publishing = false
        this.$toast.error('提交失败：' + (e.message || e))
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle('修改话题')
    }
  }
}
</script>

